// åˆ†åˆ«æ‰¾åˆ°æœ‰ key çš„å…ƒç´ ä½ç½®å’Œæ²¡æœ‰ key çš„å…ƒç´ çš„ä½ç½®
function keyIndex (list) {
  const keys = {}; // æœ‰ key çš„èŠ‚ç‚¹ä½ç½®
  const free = []; // å¯æ›¿æ¢çš„ä½ç½®ï¼ˆæ²¡æœ‰ key çš„èŠ‚ç‚¹éƒ½è¢«æ ‡è¯†ä¸ºå¯æ›¿æ¢çš„èŠ‚ç‚¹ï¼‰
  for (let i = 0; i < list.length; i++) {
    const item = list[i]
    const itemKey = item.key;
    if (typeof itemKey !== 'undefined') {
      keys[itemKey] = i;
    } else {
      free.push(i);
    }
  }
  return {
    keys: keys,
    free: free
  };
}

// æ¨¡æ‹Ÿåˆ é™¤
function remove(arr, index, key) {
  arr.splice(index, 1);
  return {
    from: index,
    key: key
  };
}

export default function reorder(pList, nList) {
  // N: pList.length
  // M: nList.length
  // O(M) time, O(M) memory
  const nListIndex = keyIndex(nList);
  const nKeys = nListIndex.keys;
  const nFree = nListIndex.free;

  if (nFree.length === nList.length) { // å¦‚æœ nList å…¨éƒ¨èŠ‚ç‚¹éƒ½æ²¡æœ‰ key å°±ä¸éœ€è¦ reorder æŠŠ nList ç›´æ¥ä½œä¸º reorder ä¹‹åçš„åˆ—è¡¨è¿”å›
    return {
      list: nList,
      moves: null
    };
  }

  // O(N) time, O(N) memory
  const pListIndex = keyIndex(pList)
  const pKeys = pListIndex.keys;
  const pFree = pListIndex.free;

  if (pFree.length === pList.length) { // å¦‚æœ pList å…¨éƒ¨èŠ‚ç‚¹éƒ½æ²¡æœ‰ key å°±ä¸éœ€è¦ reorder æŠŠ nList ç›´æ¥ä½œä¸º reorder ä¹‹åçš„åˆ—è¡¨è¿”å›
    return {
      list: nList,
      moves: null
    };
  }

  // O(MAX(N, M)) memory
  const rList = [];

  let freeIndex = 0; // è¡¨ç¤ºæ²¡æœ‰ key çš„èŠ‚ç‚¹å·²ä½¿ç”¨çš„ä¸ªæ•°
  let freeCount = nFree.length; // è¡¨ç¤º nList ä¸­æ²¡æœ‰ key çš„èŠ‚ç‚¹çš„æ€»ä¸ªæ•°
  let deletedItems = 0; // è¢«åˆ é™¤çš„èŠ‚ç‚¹çš„ä¸ªæ•°

  // O(N) time
  // éå† pList å°† pList æœ‰ key çš„èŠ‚ç‚¹æ˜ å°„åˆ° nList çš„èŠ‚ç‚¹ï¼Œå¦‚æœæ²¡æœ‰æ˜ å°„ï¼Œå°±ç”¨ null è¡¨ç¤ºèŠ‚ç‚¹å°†è¢«åˆ é™¤ã€‚pList ç©ºé—²èŠ‚ç‚¹ç”¨ nList çš„ç©ºé—²èŠ‚ç‚¹æŒ‰é¡ºåºå ä½
  for (let i = 0 ; i < pList.length; i++) {
    const pItem = pList[i];

    if (typeof pItem.key !== 'undefined') { // key èŠ‚ç‚¹
      if (nKeys.hasOwnProperty(pItem.key)) { // æœ‰æ˜ å°„
        const itemIndex = nKeys[pItem.key];
        rList.push(nList[itemIndex]);
      } else { // æ²¡æœ‰æ˜ å°„
        deletedItems++;
        rList.push(null);
      }
    } else { // ç©ºé—²èŠ‚ç‚¹
      if (freeIndex < freeCount) { // nList çš„ç©ºé—²èŠ‚ç‚¹è¿˜æ²¡ç”¨å®Œï¼Œç»§ç»­ç”¨
        const itemIndex = nFree[freeIndex++];
        rList.push(nList[itemIndex]);
      } else { // nList çš„ç©ºé—²èŠ‚ç‚¹ç”¨å®Œäº†ï¼Œè¿™ä¸ª pList çš„ç©ºé—²èŠ‚ç‚¹æ²¡æœ‰èŠ‚ç‚¹ä¸å…¶å¯¹åº”ï¼Œåº”è¯¥è¢«åˆ é™¤
        deletedItems++;
        rList.push(null);
      }
    }
  }

  const lastFreeIndex = freeIndex >= nFree.length ? // nList ä¸­ä¸‹ä¸€ä¸ªç©ºé—²èŠ‚ç‚¹çš„ä½ç½®
      nList.length : // nList ä¸­ç©ºé—²èŠ‚ç‚¹å·²ç»ç”¨å®Œäº†
      nFree[freeIndex]; // æœªç”¨å®Œ

  // O(M) time
  // éå† nList å°†æ–°å¢èŠ‚ç‚¹ï¼å‰©ä½™ç©ºé—²èŠ‚ç‚¹è¿½åŠ åˆ° rList æœ«å°¾
  for (let j = 0; j < nList.length; j++) {
    const nItem = nList[j];
    if (nItem.key) {
      if (!pKeys.hasOwnProperty(nItem.key)) {
        rList.push(nItem);
      }
    } else if (j >= lastFreeIndex) {
      rList.push(nItem);
    }
  }

  const simulateList = rList.slice(0); // å¤åˆ¶ä¸€ä»½ï¼Œæ¨¡æ‹Ÿ rList -> nList é‡æ’æ“ä½œ
  let simulateIndex = 0;
  const removes = []; // è¢«ç§»é™¤çš„èŠ‚ç‚¹
  const inserts = []; // è¢«æ’å…¥çš„èŠ‚ç‚¹
  let simulateItem;

  for (let k = 0; k < nList.length;) {
    const wantedItem = nList[k]; // ç›®æ ‡èŠ‚ç‚¹
    simulateItem = simulateList[simulateIndex]; // æ¨¡æ‹ŸèŠ‚ç‚¹

    // å…ˆæ¨¡æ‹Ÿåˆ é™¤
    while (simulateItem === null && simulateList.length) {
      removes.push(remove(simulateList, simulateIndex, null)); // åˆ é™¤ä¸éœ€è¦è®°å½• key çš„èŠ‚ç‚¹
      simulateItem = simulateList[simulateIndex];
    }

    if (!simulateItem || simulateItem.key !== wantedItem.key) {
      // å¦‚æœå½“å‰ä½ç½®æœ‰ key
      if (wantedItem.key) {
        // å¦‚æœå½“å‰èŠ‚ç‚¹çš„ä½ç½®ä¸å¯¹ï¼Œè¦è¿›è¡Œç§»åŠ¨
        if (simulateItem && simulateItem.key) {
          if (nKeys[simulateItem.key] !== k + 1) {
            removes.push(remove(simulateList, simulateIndex, simulateItem.key)); // å…ˆç§»é™¤å½“å‰ä½ç½®çš„èŠ‚ç‚¹
            simulateItem = simulateList[simulateIndex]; // åˆ é™¤åï¼Œè¯¥ä½ç½®å¯¹åº”çš„æ˜¯ä¸‹ä¸€ä¸ªèŠ‚ç‚¹
            // ç„¶ååœ¨å½“å‰ä½ç½®æ’å…¥ç›®æ ‡èŠ‚ç‚¹
            if (!simulateItem || simulateItem.key !== wantedItem.key) { // å¦‚æœåˆ é™¤ä¹‹åè¿˜ä¸å¯¹åº”ï¼Œå°±æ’å…¥ç›®æ ‡èŠ‚ç‚¹
              inserts.push({key: wantedItem.key, to: k});
            } else { // åˆ é™¤åæ­£å¥½å¯¹åº”å°±ä¸éœ€è¦æ’å…¥äº†
              simulateIndex++; // æ£€æŸ¥ä¸‹ä¸€ä¸ª
            }
          }
          else { // nKeys[simulateItem.key] === k + 1 å¦‚æœä¸‹ä¸€ä¸ªç›®æ ‡èŠ‚ç‚¹å’Œå½“å‰æ¨¡æ‹ŸèŠ‚ç‚¹å¯¹åº”
            inserts.push({key: wantedItem.key, to: k});
          }
        }
        else { // ä½ç½®ä¸å¯¹ï¼Œæ’å…¥
          inserts.push({key: wantedItem.key, to: k});
        }
        k++;
      }
      // ç›®æ ‡èŠ‚ç‚¹æ²¡æœ‰ key ä½†æ˜¯ æ¨¡æ‹ŸèŠ‚ç‚¹æœ‰ key
      else if (simulateItem && simulateItem.key) {
        // ä½ç½®ä¸å¯¹ï¼Œåˆ é™¤
        removes.push(remove(simulateList, simulateIndex, simulateItem.key))
      }
    }
    else {
      simulateIndex++;
      k++;
    }
  }

  // åˆ é™¤æ‰€æœ‰å‰©ä½™èŠ‚ç‚¹
  while(simulateIndex < simulateList.length) {
    simulateItem = simulateList[simulateIndex];
    removes.push(remove(simulateList, simulateIndex, simulateItem && simulateItem.key));
  }

  // è¿™ç§æƒ…å†µä¸éœ€è¦ç§»ä½ï¼Œåªéœ€è¦åˆ é™¤å¤šä½™çš„èŠ‚ç‚¹ï¼šæ²¡æœ‰ key å¯¹åº”çš„èŠ‚ç‚¹ã€å¤šä½™çš„ç©ºé—²èŠ‚ç‚¹
  if (removes.length === deletedItems && !inserts.length) {
    return {
      list: rList,
      moves: null
    };
  }

  return {
    list: rList,
    moves: {
      removes: removes,
      inserts: inserts
    }
  };
}

